  background-color: linear-gradient(to bottom,#5cb85c 0,#419641 100%);
  background-repeat: repeat-x;
  border-color: #419641;

  font-size: 14px;

  color: white;

  -webkit-user-select: none;

  cursor: pointer;

  width: 120px;
