.mySlider label[_v-df99c510] {
  display: inline-block;
  width: 100px;
  height: 30px;
  border: 3px solid rgba(0,0,0,0.07);
  border-radius: 17px;
  position: relative;
  box-shadow:  inset 1px 1px 1px 1px rgba(0,0,0,0.4), 0px 0px 0px 1px rgba(0,0,0,0.1), 0px 0px 0px 2px rgba(0,0,0,0.1), 0px 0px 4px 2px rgba(0,0,0,0.07);
  margin: 0px;
}

.mySlider input[_v-df99c510] {
  display: none;
}

.mySlider input:checked ~ a[_v-df99c510] {
  left: 50%;
}

.mySlider input:checked ~ div span[_v-df99c510] {
  background-color: rgba(0,158,255,0.5);
}

.mySlider label span[_v-df99c510] {
  z-index: 1;
  position: absolute;
  display: inline-block;
  height: 24px;
  left: 0;
  width: 50%;
  border-radius: 15px 0px 0px 15px;
  border-color: rgba(0,0,0,0.1);
  border-style: solid;
  border-width: 1px 0px 1px 1px;
  background-color: rgba(200,200,200,0.5);
  background-image:-webkit-linear-gradient(90deg, rgba(0,0,0,0.1) 0%, rgba(0,0,0,0.0) 100%);
  text-align: center;
  line-height: 30px;
  font-family: helvetica, sans-serif;
  font-size: 14px;
  font-weight: 800;
  color: #555;
  text-shadow: 0px 1px white;
  -webkit-transition: all 1.0s ease-in-out;
  transition: all 1.0s ease-in-out;
  

}

.mySlider label span.no[_v-df99c510] {

  left: 50%;  
  border-radius: 0px 15px 15px 0px;     
  border-width: 1px 1px 1px 0px;

}

.mySlider .slider[_v-df99c510] {
  display: inline-block;
  position: absolute;
  width: 50%;
  height: 24px;
  background-color: #efefef;
  left: 0%;
  border-radius: 30px;
  z-index: 2;
  border: 1px solid rgba(0,0,0,0.2);
  box-shadow: inset 0px 0px 5px 1px rgba(0,0,0,0.1), 0px 1px 1px 0px rgba(0,0,0,0.2);
  -webkit-transition: all 0.5s ease-in-out;
  transition: all 0.5s ease-in-out;

}

.mySlider .slider[_v-df99c510]:after, .mySlider .slider[_v-df99c510]:before {
  content: "";
  width: 30%;
  height: 25px;
  position: absolute;
  top: 2px;
